/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 2.4rem;
  height: 1.4rem;
  margin: 0 0.2rem;
}

.mode-switcher {
  vertical-align: middle;
}

.mode-switcher svg {
  font-size: 1.5rem;
}

/* Hide default HTML checkbox */
.switch input {
  display: none;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 1rem;
  width: 1rem;
  left: 0.2rem;
  bottom: 0.2rem;
  transition: 0.4s;
}

.slider:hover {
  background-color: #328fff;
}

input:focus + .slider {
  box-shadow: 0 0 1px #328fff;
}

input:checked + .slider:before {
  transform: translateX(1rem);
}

